import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { retrieveProducts } from "./thunks";

import ETypes, { ProductState, IProduct, ITreatedProduct } from "./types";

const initialState: ProductState = {
    pending: true,
    products: [],
    error: null,
}

export default createSlice({
    name: ETypes.SLICE_NAME,
    initialState,
    reducers: {},
    extraReducers: (builder) =>
        builder
            .addCase(retrieveProducts.pending, (state) => ({
                ...state,
                pending: true
            }))
            .addCase(retrieveProducts.rejected, (state) => ({
                ...state,
                pending: false,
                error: "Error loading products"
            }))
            .addCase(retrieveProducts.fulfilled, (state, { payload }: PayloadAction<IProduct[]>) => {
                const products: ITreatedProduct[] = [
                    ...payload.map(product => {
                        return {
                            ...product,
                            // creates field values that is a object with property_values.id as key 
                            values: product.property_values.reduce((obj, item) => Object.assign(obj, { [item.property_id]: item.value }), {})
                        }
                    })
                ];
                return {
                    ...state,
                    pending: false,
                    products,
                }
            })
})