import { createAsyncThunk } from "@reduxjs/toolkit";
import { retrieveProducts } from "../entities/products/thunks";
import { retrieveProperties } from "../entities/properties/thunks";
import ETypes from "./types";

export const retrieveData = createAsyncThunk(
    ETypes.THUNK_RETRIEVE_DATA, 
    (_, {dispatch}) => {
        dispatch(retrieveProducts());
        dispatch(retrieveProperties());
    }
);
