import { Option, PropertyOption } from "../models/models";
import { ITreatedProduct } from "../store/modules/entities/products/types";

export const getOperatorsByProperty = (property: PropertyOption | null) => {
  const operators = window.datastore
    .getOperators()
    .map((o: { text: string; id: string }) => ({
      label: o.text,
      value: o.id,
    })) as Option[];

  switch (property?.type) {
    case "string":
      return operators.filter(
        (o: Option) => o.value !== "greater_than" && o.value !== "less_than"
      );
    case "number":
      return operators.filter((o: Option) => o.value !== "contains");
    case "enumerated":
      return operators.filter(
        (o: Option) =>
          o.value !== "greater_than" &&
          o.value !== "less_than" &&
          o.value !== "contains"
      );
    default:
      return [];
  }
};

const equals = (products: ITreatedProduct[], property: number, value: any) =>
  products.filter(
    (prod) =>
      prod.values[property] === value || prod.values[property] === Number(value)
  );

const greater_than = (
  products: ITreatedProduct[],
  property: number,
  value: any
) =>
  products.filter(
    (prod) =>
      !isNaN(Number(prod.values[property])) &&
      Number(prod.values[property]) > Number(value)
  );

const less_than = (products: ITreatedProduct[], property: number, value: any) =>
  products.filter(
    (prod) =>
      !isNaN(Number(prod.values[property])) &&
      Number(prod.values[property]) < Number(value)
  );

const any = (products: ITreatedProduct[], property: number, _: any) =>
  products.filter((prod) => !!prod.values[property]);

const none = (products: ITreatedProduct[], property: number, _: any) =>
  products.filter((prod) => !prod.values[property]);

const in_of = (products: ITreatedProduct[], property: number, value: any) =>
  products.filter((prod) =>
    (value as Array<string | number>).includes(prod.values[property].toString())
  );

const contains = (products: ITreatedProduct[], property: number, value: any) =>
  products.filter((prod) =>
    prod.values[property]
      .toString()
      .toLocaleUpperCase()
      .includes(value.toString().toLocaleUpperCase())
  );

export const filterUtils = {
  equals,
  greater_than,
  less_than,
  any,
  none,
  in: in_of,
  contains,
};
