// External
import React, { useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Grid,
  Autocomplete,
  TextField,
  Button,
  Select,
  FormControl,
  InputLabel,
  MenuItem,
} from "@mui/material";

// Styles
import "./Filters.css";

// Models
import { Option, PropertyOption } from "../../models/models";

// Utils
import { getOperatorsByProperty } from "../../utils/utils";

// Store
import filterEntity from "../../store/modules/entities/filters";
import { getProductValuesByProperty } from "../../store/modules/main/selectors";
import { getProperties } from "../../store/modules/entities/properties/selectors";
import {
  selectedOperator,
  selectedProperty,
  selectedValue,
} from "../../store/modules/entities/filters/selectors";

const Filters = () => {
  const [operators, setOperators] = useState<Option[]>([]);

  const dispatch = useDispatch();

  const property = useSelector(selectedProperty);
  const operator = useSelector(selectedOperator);
  const value = useSelector(selectedValue);
  const properties = useSelector(getProperties);
  const renderValues = useSelector(getProductValuesByProperty(property));

  const renderProperties = properties.map(
    (p) => ({ label: p.name, value: p.id, type: p.type } as PropertyOption)
  );

  /** onChange handlers */
  const propertyHandler = (ev: any) => {
    dispatch(filterEntity.actions.updateProperty(ev.target.value));
    setOperators(
      getOperatorsByProperty(
        renderProperties.find((p) => p.value === Number(ev.target.value)) ||
          null
      )
    );
  };
  const operatorHandler = (ev: any) => {
    dispatch(filterEntity.actions.updateOperator(ev.target.value));
  };
  const multipleValuesHandler = (ev: any) => {
    const { options } = ev.target;
    const selectedArray = [];
    for (let i = 0; i < options.length; i++) {
      if (options[i].selected) selectedArray.push(options[i].value);
    }
    dispatch(filterEntity.actions.updateValue(selectedArray));
  };
  const valueHandler = (ev: any) =>
    dispatch(filterEntity.actions.updateValue(ev.target.value));
  const clearHandler = () => dispatch(filterEntity.actions.clearFilter());

  /** Render value field depending on operator */
  const renderValue = useMemo(() => {
    switch (operator) {
      case "in":
        return (
          <FormControl className="filter-drop-form">
            <InputLabel
              className="input-label"
              shrink
              htmlFor="select-multiple-native"
            >
              {"Choose value(s)"}
            </InputLabel>
            <Select
              multiple
              native
              value={value || []}
              onChange={multipleValuesHandler}
              label="Choose value(s)"
              inputProps={{
                id: "select-multiple-native",
              }}
            >
              {renderValues.map((value) => (
                <option key={value?.value} value={value?.value}>
                  {value?.label}
                </option>
              ))}
            </Select>
          </FormControl>
        );
      case "equals":
      case "contains":
      case "greater_than":
      case "less_than":
        return (
          <TextField
            id="value-input"
            data-testid="value-input"
            label="Value"
            variant="outlined"
            onChange={valueHandler}
            value={value ?? ""}
            className="filter-value-text"
            placeholder="Value"
          />
        );
      default:
        return null;
    }
    // eslint-disable-next-line
  }, [property, operator, value]);

  return (
    <Grid container className="filters">
      <Grid item xs={12} sm={6} md={3} className="filter-drop">
        <FormControl fullWidth>
          <InputLabel id="label-property-select-label">
            {"Select a Property"}
          </InputLabel>
          <Select
            labelId="property-select-labelId"
            id="property-select"
            data-testid="property-select"
            value={property ?? ""}
            label="Select a Property"
            onChange={propertyHandler}
          >
            {renderProperties.map((p) => (
              <MenuItem key={p.value} value={p.value}>
                {p.label}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Grid>
      <Grid item xs={12} sm={6} md={3} className="filter-drop">
        {property !== null && (
          <FormControl fullWidth>
            <InputLabel id="label-operator-select-label">
              {"Select an Operator"}
            </InputLabel>
            <Select
              labelId="operator-select-labelId"
              id="operator-select"
              data-testid="operator-select"
              value={operator ?? ""}
              label="Select an Operator"
              onChange={operatorHandler}
            >
              {operators.map((p) => (
                <MenuItem key={p.value} value={p.value}>
                  {p.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        )}
      </Grid>
      <Grid item xs={12} sm={6} md={3} className="filter-drop">
        {renderValue}
      </Grid>
      <Grid item xs={12} sm={6} md={3} className="filter-button">
        <Button
          variant="outlined"
          className="button-mui"
          onClick={clearHandler}
        >
          {"Clean filters"}
        </Button>
      </Grid>
    </Grid>
  );
};

export default Filters;
