// External
import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import { Grid } from "@mui/material";
import {
  Table,
  TableHead,
  TableBody,
  TableCell,
  TableRow,
} from "@mui/material";

// Styles
import "./ProductsTable.css";

// Store
import { getProperties } from "../../store/modules/entities/properties/selectors";
import { getFilteredProducts } from "../../store/modules/main/selectors";

const ProductsTable = () => {
  const products = useSelector(getFilteredProducts);
  const properties = useSelector(getProperties);

  /** render table head */
  const tableHead = useMemo(() => {
    return properties.map((property) => {
      return (
        <TableCell key={property.id}>
          <span className="table-head-item">{property.name}</span>
        </TableCell>
      );
    });
  }, [properties]);

  /** render table body */
  const tableBody = useMemo(
    () =>
      products.map((product) => (
        <TableRow key={product.id}>
          {properties.map((property, idx) => {
            return (
              <TableCell key={product.values[property.id] + idx.toString()}>
                <span>{product.values[property.id]?.toString()}</span>
              </TableCell>
            );
          })}
        </TableRow>
      )),
    [products, properties]
  );

  return (
    <Grid item xs={12}>
      <Grid item xs={12} className="table-title-item">
        <span className="table-title">Products</span>
      </Grid>
      <Table id="product-table" className="table-container">
        <TableHead id="product-table-head" className="table-head">
          <TableRow>{tableHead}</TableRow>
        </TableHead>
        <TableBody id="product-table-body" className="table-body">
          {tableBody}
        </TableBody>
      </Table>
    </Grid>
  );
};

export default ProductsTable;
