import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import ETypes, { FilterState } from "./types";

const initialState: FilterState = {
    selectedPropertyId: null,
    selectedOperatorId: null,
    selectedValue: null,
}

export default createSlice({
    name: ETypes.SLICE_NAME,
    initialState,
    reducers: {
        updateProperty: (state, { payload }: PayloadAction<number | null>) => {
            if (payload === null) return { ...state, selectedPropertyId: null, selectedOperatorId: null, selectedValue: null };
            return { ...state, selectedPropertyId: payload };
        },
        updateOperator: (state, { payload }: PayloadAction<string | null>) => {
            if (payload === null) return { ...state, selectedOperatorId: null, selectedValue: null };
            return { ...state, selectedOperatorId: payload, selectedValue: null };
        },
        updateValue: (state, { payload }: PayloadAction<string | number | null | Array<string | number>>) =>
            ({ ...state, selectedValue: payload }),
        clearFilter: (state) => ({ ...state, ...initialState }),
    },
    extraReducers: () => { },
})