import { createSelector } from "@reduxjs/toolkit";

import { RootState } from "../../../store";
import ETypes from "./types";

/**
 * Returns the self redux slice
 * @param {RootState} { [ETypes.SLICE_NAME]: Slice}
 */
const selectSelf = ({ [ETypes.SLICE_NAME]: Slice }: RootState) => Slice;

export const selectedProperty = createSelector(selectSelf, ({ selectedPropertyId }) => selectedPropertyId);

export const selectedOperator = createSelector(selectSelf, ({ selectedOperatorId }) => selectedOperatorId);

export const selectedValue = createSelector(selectSelf, ({ selectedValue }) => selectedValue);
