import { createSelector } from "@reduxjs/toolkit";
import { filterUtils } from "../../../utils/utils";

import { RootState } from "../../store";
import filterEntity from "../entities/filters";
import productsEntity from "../entities/products";
import propertiesEntity from "../entities/properties";
import ETypes from "./types";

/**
 * Returns the self redux slice
 * @param {RootState} { [ETypes.SLICE_NAME]: Slice}
 */
const selectSelf = ({ [ETypes.SLICE_NAME]: Slice }: RootState) => Slice;

export const isLoading = createSelector(selectSelf, ({ loading }) => loading);

export const hasException = createSelector(
  selectSelf,
  ({ exception }) => exception
);

export const getProductValuesByProperty = (propertyId: number | null) =>
  createSelector(
    productsEntity.selectors.getProducts,
    propertiesEntity.selectors.getProperties,
    (products, properties) => {
      if (propertyId === null) return [];

      // if property is enumerated, retrieve values from properties array
      const findProperty = properties.find((p) => p.id === propertyId);
      if (findProperty?.name === "enumerated")
        return (
          findProperty?.values?.map((el) => ({ value: el, label: el })) || []
        );

      // get an array of values and remove null & empty (absence of value)
      const values = products
        .map((product) => product.values[propertyId])
        .filter((value) => !!value && value !== "")
        .sort();

      // remove duplicates and map into an options array
      return [...new Set(values)].map((el) => ({ value: el, label: el }));
    }
  );

export const getFilteredProducts = createSelector(
  productsEntity.selectors.getProducts,
  filterEntity.selectors.selectedProperty,
  filterEntity.selectors.selectedOperator,
  filterEntity.selectors.selectedValue,
  (products, property, operator, value) => {
    if (
      property !== null &&
      operator !== null &&
      (value !== null || operator === "any" || operator === "none")
    )
      return filterUtils[operator as keyof typeof filterUtils](
        products,
        property,
        value
      );

    return products;
  }
);
