import React from 'react'
import { Grid } from "@mui/material";

import "./Products.css";
import Filters from '../../components/filters'
import ProductsTable from '../../components/productsTable'

const Products = () => {
  return (
    <Grid item xs={12} className="products">
      <Filters />
      <ProductsTable />
    </Grid>
  )
}

export default Products