import React, { useEffect } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { Grid, CircularProgress } from "@mui/material";

import './App.css';
import Products from './containers/products';
import { getProducts, isLoadingProducts } from './store/modules/entities/products/selectors';
import { isLoadingProperties } from './store/modules/entities/properties/selectors';
import { retrieveData } from './store/modules/main/thunks';

const App = () => {
  const dispatch = useDispatch();

  const loadingProducts = useSelector(isLoadingProducts);
  const loadingProperties = useSelector(isLoadingProperties);
  const prod = useSelector(getProducts);

  useEffect(() => {
    dispatch(retrieveData());
  }, [])

  return (
    <Grid container className="App">
      {loadingProducts || loadingProperties ?
        <Grid item xs={12}>
          <CircularProgress />
        </Grid>
        : <Products />}
    </Grid>
  );
}

export default App;
