import { createSlice } from "@reduxjs/toolkit";
import { retrieveProperties } from "./thunks";

import ETypes, { PropertyState } from "./types";

const initialState: PropertyState = {
    pending: true,
    properties: [],
    error: null,
}

export default createSlice({
    name: ETypes.SLICE_NAME,
    initialState,
    reducers: {},
    extraReducers: (builder) =>
        builder
            .addCase(retrieveProperties.pending, (state) => ({
                ...state,
                pending: true
            }))
            .addCase(retrieveProperties.rejected, (state) => ({
                ...state,
                pending: false,
                error: "Error loading products"
            }))
            .addCase(retrieveProperties.fulfilled, (state, { payload }) => ({
                ...state,
                pending: false,
                properties: payload.sort((a, b) => a.id - b.id)
            }))
})