export interface IProductProperty {
    property_id: number;
    value: string;
}

export interface IProduct {
    id: number;
    property_values: IProductProperty[];
}

export interface ITreatedProduct {
    id: number;
    property_values: IProductProperty[];
    values: { [key: number]: string | number };
}

export interface ProductState {
    pending: boolean;
    products: ITreatedProduct[];
    error: string | null;
}

enum ETypes {
    SLICE_NAME = "Products",
    THUNK_RETRIEVE_PRODUCTS = "Products/RetrieveProducts",
}

export default ETypes;
