import { createSelector } from "@reduxjs/toolkit";

import { RootState } from "../../../store";
import ETypes from "./types";

/**
 * Returns the self redux slice
 * @param {RootState} { [ETypes.SLICE_NAME]: Slice}
 */
const selectSelf = ({ [ETypes.SLICE_NAME]: Slice }: RootState) => Slice;

export const isLoadingProducts = createSelector(selectSelf, ({ pending }) => pending);

export const isError = createSelector(selectSelf, ({ error }) => !!error);

export const getProducts = createSelector(selectSelf, ({ products }) => products);

